import * as React from "react"
import { StaticImage } from "gatsby-plugin-image"
import { Link } from "gatsby"
import { getOppositeLanguageUrl } from "./getOppositeLanguageUrl";

const PageHeader = ({ language, location }) => {
  const [isMenuOpen, setIsMenuOpen] = React.useState(false);
  return (
    <header id="page-hader">
      <div className="container">
        <Link to={language === 'en' ? '/' : `/${language}/`}>
          <img id="header-logo" src={`${__PATH_PREFIX__}/img/logo-horizontal-inverse-filled.svg`}
            width={373}
            height={38}
            alt="Almot치sim"
          />
        </Link>
        <button id="burger-menu" onClick={() => setIsMenuOpen(value => !value)}><img id="burger-button" src={`${__PATH_PREFIX__}/img/burger-menu-white.svg`} /></button>
        <nav id="site-navigation" className={isMenuOpen ? 'menu-open' : undefined}>
          <div className="container">
            {language === 'en' ?
              <ul>
                <li><Link to={`${__PATH_PREFIX__}/${language}/projects/`}>Projects</Link></li>
                <li><Link to={`${__PATH_PREFIX__}/${language}/sketches/`}>Sketches</Link></li>
                <li><Link to={`${__PATH_PREFIX__}/${language}/texts/`}>Texts</Link></li>
                <li><Link to={`${__PATH_PREFIX__}/${language}/about/`}>About</Link></li>
                <li><Link to={getOppositeLanguageUrl(location.pathname, language)}>Espa침ol</Link></li>
              </ul> : <ul>
                <li><Link to={`${__PATH_PREFIX__}/${language}/projects/`}>Proyectos</Link></li>
                <li><Link to={`${__PATH_PREFIX__}/${language}/sketches/`}>Bosquejos</Link></li>
                <li><Link to={`${__PATH_PREFIX__}/${language}/texts/`}>Textos</Link></li>
                <li><Link to={`${__PATH_PREFIX__}/${language}/about/`}>Info</Link></li>
                <li><Link to={getOppositeLanguageUrl(location.pathname, language)}>English</Link></li>
              </ul>}
          </div>
        </nav>
      </div>
    </header>
  )
}

export default PageHeader;